<template>
<div id="login_pic">
  <div class="form">
  
  <b-form>
    <label
      class="mb-4 Do_Hyeon Big_font"
    >
      
    </label>
    <label class="sr-only" for="login_id">LoginID</label>
    <b-input
      id="login_id"
      v-model="user.id"
      placeholder=""
    />
    <label class="sr-only" for="login_password">LoginPassword</label>
    <b-input
      id="login_password"
      v-model="user.password"
      type="password"
      placeholder=""
      class="mt-3"
    >
    </b-input>

    <b-button block class="mt-3 Nunum_Noto" @click="login"></b-button>
  </b-form>
  </div>
</div>
</template>

<script>
import EventBus from "../../EventBus"

export default {
  data () {
    return {
      user: {
      	id: '',
      	password: '',
      	name: ''
			},
      password_check: '',
      show_login: true
    }   
  },

  methods: {
  //  
  login: function (event) {
    this.$http.post('/api/SignIn/check', {
      user: this.user
    })
    .then(
      (response) => {  // 
        alert(' ')

        //      
        EventBus.$emit('triggerEventBus', response.data.level )

        this.$router.push('/')
        this.$router.go()
      },
      (error) => { // error  
        alert(error.response.data.error)
      }
    )
    .catch(error => {
      alert(error)
    })
  },


  //   
	signUp: function (event) {
    //   
    if(this.user.password != this.password_check) {
      alert('  .')
      // this.$router.push('/login-page')          //  () 
      return;
    }
    
    //     
    this.$http.post('/api/SignIn/signUp', { //axios 
      user: this.user
    })
    .then((response) => {
      //  
      if (response.data.result == false) {
        alert(',   ')
      }
      //  
      else if (response.data.result == true) {
        alert(' ')
        // this.$router.push('/login-page')    //   
      }
    },
    (error) => { // error  
      alert(error.response.data.error)
    })
    .catch(error => {
      alert(error)
    })
  }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR');

#login_pic {
	width: 100%;
	background:linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.3)), url('../../pic/login_pic.jpg'), center, center fixed;
	background-size:cover;
	background-repeat: no-repeat;
	overflow: hidden;
  min-height: calc(100vh - 50px);
}

.form {
	position: relative;
  z-index: 1;
  background: #FFFFFF;
  border-radius: 10px;

  max-width: 400px;

  margin: auto auto;
  margin-top: 13%;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

button {
  background-color: orange;
  border: none;
  font-size: 17px;
  font-weight: bold;
}

</style>



