<template>
  <div id="Home">
    <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="4000"
      controls
      indicators
      background="#ababab"
      class="cccc"
      img-height="300"

      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <!-- Text slides with image -->
      <b-carousel-slide
        :img-src="require('../pic/main_1.jpg')"
      >
        <div>
          <h1 class="letter"></h1>
          <p class="Nunum_Noto letter">     <br/>
                </p>
          <b-button class="Nunum_Noto" ></b-button>
        </div>
      </b-carousel-slide>

      <b-carousel-slide 
        :img-src="require('../pic/main_2.jpg')">
        <div>
          <h1 class="letter"> </h1>
          <p class="Nunum_Noto letter">
               . <br/>
                
          </p>
        </div>
      </b-carousel-slide>

      <b-carousel-slide 
        :img-src="require('../pic/main_3.jpg')">
        <h1 class="letter"> </h1>
          <p class="Nunum_Noto letter">
              ,   .<br/>
                 .
          </p>
      </b-carousel-slide>

    </b-carousel>
    <div>
      <b-container class=" mt-3">
        <b-row>
          <b-col class="media_800_min_100 mt-3">
            <b-img fluid :src="require('../pic/weak_point.jpg')" />
          </b-col>
          <b-col class="media_800_min_100 mt-2">
            <h3 class="Nunum_Noto">  ?</h3>
            <p class="mt-3">
                   ,                . ,              . ,                 .
            </p>
          </b-col >
        </b-row>

        <b-row class="mt-4">
          <b-col class="media_800_min_100 min_w_360">
            <h3 class="Nunum_Noto">  ?</h3>
            <p class="mt-3">
                  ?        ?           .                   .
            </p>
          </b-col>
          <b-col class="media_800_min_100 min_w_360">
            <b-img fluid :src="require('../pic/how_teach.jpg')" />
          </b-col>
        </b-row>
      </b-container>
    </div>

    <div class="parallax-wrapper">
      <!-- <b-img fluid :src="require('../pic/main_parallax.jpg')" /> -->
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      show: true,
      sliding: false,
      slide: 0,
    }
  },

  methods: {
     onSlideStart(slide) {
       this.sliding = true
     },
     onSlideEnd(slide) {
       this.sliding = false
     }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
@import url('https://fonts.googleapis.com/css?family=Righteous');

#Home {
  /* margin-top: 60px; */
  align-items: center;
  width: 100%;
  /* background-color: #0c0524; */
  /* color: white; */
}

.carousel-caption {
  position: absolute;
  font-size: 1.5vw;
  text-align: left;
  top: 40%;
  bottom: 0;
}

.carousel-caption h1{
  font-size: 4vw;
}

#carousel-1 {
  max-height: 500px;
  overflow: hidden;
}

#carousel-1 img {
  background-size: contain;
}

#carousel-1 button {
  background-color: rgb(211, 137, 0);
  border: none;
  font-size: 1vw;
}

/* carousel footer  */
#carousel-1___BV_indicators_ {
  visibility: hidden;
}

/* ==================  ================== */
.letter {
  color: white;
  font-weight: bold;
  position: relative;
  /* text-shadow: 0 0 3px white; */
  animation: fadein 2s
}
  
@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}


/*   carousel     */
@media screen and (min-width: 1600px) {
  .carousel-caption {
     font-size: 25px;
  }

  .carousel-caption {
    top: 100px;
  }
  
}
@media screen and (max-width: 800px) {
  .media_800_min_100 {
    min-width:100%;
  }

  .carousel-caption h1{
    font-size: 6vw;
  }

  .carousel-caption {
    top: 15%;
  }

  #carousel-1 button {
    visibility: hidden;
  }

}

</style>



