<template>
	<div id="navbar_wrap">
		<div id="navbar">
			<!--  -->
			<span class="logo">
				<router-link to="/">
					<img src="@/assets/logo.png"/>
					<img id="logo_name" src="@/assets/logo_name.png">
				</router-link>
			</span>

			<!--    -->
			<div class="nav-tabs">

				<!--   '',   ''   -->
				<span v-if="this.user.name == null">
					<router-link to="/login-page"></router-link>
				</span>
				<span id="welcome" v-if="this.user.name != null">
					<p class="m-0"> {{ this.user.name }} </p>
				</span>

				<span v-if="this.user.name != null">
					<!-- router-link  v-on    .native  -->
					<router-link @click.native="logout_fun" to="/logout"></router-link>
				</span>

				<!--   '',   ''   -->
				<span v-if="this.user.level<3">
					<router-link to="/add-person-page"></router-link>
				</span>

				<span v-if="this.user.level<4">
					<router-link to="/history-page"></router-link>
				</span>

				<span v-if="this.user.level<3">
					<router-link to="/manage-page"></router-link>
				</span>

				<!-- <span>
					<router-link to="/signup-page">t</router-link>
				</span> -->

				<!-- <span>
					<router-link to="/schedule-page">t</router-link>
				</span> -->

				<!-- <span v-for="item in must_be_login_menu" :key="item.id">
					<router-link @click.native="check_login" v-bind:to="item.link">{{ item.text }}</router-link>
				</span> -->

				<span>
					<router-link to="/body-check"></router-link>
				</span>

				<span>
					<router-link to="/exercise-page"></router-link>
				</span>
			</div>

			<div id="icon_menu" @click="toggle_menu()">
  			<div class="bar1"></div>
  			<div class="bar2"></div>
  			<div class="bar3"></div>
			</div>
		</div>
		<div>
			<b-collapse id="mobile_menu">
				<div v-if="this.user.name == null" @click="toggle_menu">
						<router-link to="/login-page" tag="button"></router-link>
				</div>

				<div v-if="this.user.name != null" @click="toggle_menu">
					<p class="m-0"> {{ this.user.name }} </p>
					<!-- router-link  v-on    .native  -->
					<router-link to="/logout" @click.native="logout_fun" tag="button"></router-link>
				</div>

				<div v-if="this.user.level<3" @click="toggle_menu">
					<!--   '',   ''   -->
					<router-link to="/add-person-page" tag="button"></router-link>
				</div>

				<div v-if="this.user.level<4" @click="toggle_menu">
					<router-link to="/history-page" tag="button"></router-link>
				</div>

				<div v-if="this.user.level<3" @click="toggle_menu">
					<router-link to="/manage-page" tag="button"></router-link>
				</div>
				
				<div @click="toggle_menu">
					<router-link to="/body-check" tag="button"></router-link>
				</div>

				<div @click="toggle_menu">
					<router-link to="/exercise-page" tag="button"></router-link>
				</div>				
			</b-collapse>
		</div>
  </div>
</template>


<script>
export default {
	data: function() {
		return {
			user: {
				name: '',
				level: 4
			},
		}
	},
	
	created() {
		this.$http.post('/api/process/session_check'
			).then(
				(response) => {
					this.user = response.data;
				},
				(error) => {
					console.log(' ')
					this.user.name = null
				}
			)
	},
	
	methods: {
		
		//  
    logout_fun: function (event) {
			console.log('')
			this.user = {}			//   '' '' 

      this.$http.post('/api/process/logout').then(
				(response) => {  			//  
					this.user.name = null
					this.$router.push('/logout')
        },
        (error) => { 					// error  
          alert(error.response.data.error)
					this.$router.push('/')
        }
      )
    },

		toggle_menu: function() {

			//   
			document.getElementById('icon_menu').classList.toggle("change");

			//   open / close
			this.$root.$emit('bv::toggle::collapse', 'mobile_menu');
		}
	}
}
</script>


<style>
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+KR');

#navbar_wrap {
	position: fixed;
	width: 100%;
	background-color: orange;
	text-align: center;
	z-index: 100;
}

#navbar {
	width: 100vw;
	max-width: 1200px;
	margin: 0 auto;
	background-color: orange;
	height: 50px;
}

#navbar > .logo img{
	margin-left: 20px;
	float: left;
	width: auto;
	height: 100%;
}

#navbar > .logo img:nth-child(2){
	margin-left: 0;
	float: left;
	width: auto;
	height: 100%;
}

.nav-tabs {
	float:right;		/*   */
	display: inline-flex;
  align-items: center;
  height: 100%;
	width: auto;
	font-size : 20px;
	margin-left: 20px;
	margin-right: 40px;
	font-family: 'Noto Sans KR', sans-serif;
}

.nav-tabs span a {
	text-decoration: none; /*    */
	color : rgb(63, 63, 63);			/*    */
}

/*      */
.nav-tabs {
	border-bottom: none;
}

.nav-tabs span a:hover {
	color : rgb(24, 45, 163);
	font-weight : bold
}

.nav-tabs > span {
  margin : 8px;
	align-items: center;
}

#welcome {
	font-size: 15px;
}

/* ================   ================= */
#icon_menu {
  display: inline-block;
  cursor: pointer;
	margin: -9999px;
}

.bar1, .bar2, .bar3 {
  width: 35px;
  height: 5px;
  background-color: #333;
  margin: 6px 0;
  transition: 0.4s;
}

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {opacity: 0;}

.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px, -8px);
  transform: rotate(45deg) translate(-8px, -8px);
}

#mobile_menu {
	display: none;
}

#mobile_menu button {
	width:100%;
	background-color: rgb(230, 120, 0);
	border: none;
	border-bottom: solid #783c00;
	margin: 0;
	text-align: left;

	font-family: 'Noto Sans KR', sans-serif;
	text-decoration: none; /*    */
	color : rgb(63, 63, 63);			/*    */
}

#mobile_menu button:hover {
	background-color: rgb(250, 120, 0);
}

/* ======================   ================ */

@media screen and (max-width : 910px) {
	.nav-tabs {
		display: none;
	}

	#navbar {
		justify-content:space-between;
	}
	
	#icon_menu {
	  /* display: inline-block;
	  cursor: pointer; */
		float:right;		/*   */

		margin-top: 4px;
		margin-right:25px;
	}

	#mobile_menu {
		display:block;
	}

	#mobile_menu:active {
		
	}
	
}

@media screen and (max-width : 500px) {
	#logo_name {
		visibility: hidden;
	}
}
	

</style>


